html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}
body {
  font-family: sans-serif;
  font-size: 16px;
  background: #bbbb37;
  color: #00003a;
}
a {
  text-decoration: none;
  color: #00003a;
  padding-bottom: 1px;
  border-bottom: solid transparent 1px;
}
a:hover,
a:active {
  border-color: #00003a;
}
h2,
h3 {
  font-weight: normal;
}
h2 {
  font-size: 28px;
  border-bottom: 2px solid #00003a;
  padding-bottom: 3px;
  display: inline-block;
}
h3 {
  font-size: 22px;
  margin: 20px 0 10px;
}
ul {
  list-style: none;
  margin: 0;
}
ul,
ul li {
  padding: 0;
}
ul li {
  margin: 0 0 5px;
}
nav,
section {
  max-width: 800px;
}
nav {
  margin: 50px auto;
}
section {
  margin: 0 auto;
}
section + section {
  margin-top: 50px;
}
section:last-child {
  margin-bottom: 50px;
}
label {
  margin: 0 0 10px;
  display: block;
}
input {
  background: transparent;
  border: 1px solid #00003a;
  padding: 2px 5px;
}
.page {
  margin: 10px;
}
.flex {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-end;
}
.nav-list li {
  margin: 0 5px;
}
.nav-list li.nav-list__item--current a {
  border-color: #00003a;
}
